<template>
  <div id="ring-chart" ref="chartRef"></div>
</template>

<script>
import chart from '@/etc/echarts'
import dayjs from 'dayjs'
import { DOC_USAGE_NAME_MAP as USAGE_MAP } from '@/assets/js/consts/common'

const DOC_USAGE_NAME_MAP = { ...USAGE_MAP, 7: '试题组和\n互动资料' }

const PRIMARY_COLOR = '#666'

export default {
  props: {
    color: {
      type: Array, default: () => ['#00b8ff', '#2bcf99', '#f9ad71', '#6ed5fd', '#8fe1c5', '#f6d7bf', '#f97197'],
    },
    date: { type: String, default: '' },
    total: { type: Number, default: 0 },
    kinds: { type: Array, default: () => [] },
  },
  methods: {
    genOption() {
      return {
        color: this.color,
        title: {
          text: `当前校本资源总量: ${this.total}`,
          subtext: `截止到${dayjs(this.date).format('YYYY/MM/DD')}`,
          x: 'center',
        },
        tooltip: {
          show: false,
        },
        legend: {
          orient: 'vertical',
          data: Object.entries(DOC_USAGE_NAME_MAP).map(([, name]) => ({ name })),
          right: 20,
          bottom: 10,

          itemWidth: 12,
          itemHeight: 12,
          itemGap: 6,
          formatter: (param) => {
            const kind = Object.entries(DOC_USAGE_NAME_MAP).find(([, name]) => param === name)[0]
            const countMaxLength = `${Math.max(...this.kinds.map(({ count }) => count))}`.length
            const { count, percent } = this.kinds.find(ele => ele.kind === parseInt(kind))
            const cnt = `${' '.repeat(countMaxLength - count.toString().length)}${count}`
            const pct = `${percent >= 10 ? '' : ' '}${percent.toFixed(1)}`
            return `${param}:${cnt} ${pct}%`
          },
          textStyle: {
            fontFamily: 'Consolas,Monaco,monospace',
            lineHeight: 14,
            rich: {},
          },
        },
        series: [
          {
            name: '',
            type: 'pie',
            radius: ['40%', '50%'],
            center: ['30%', '60%'],
            legendHoverLink: false,
            hoverAnimation: false,
            data: this.kinds.map(({ kind, count }, index) => ({
              name: DOC_USAGE_NAME_MAP[kind] || kind,
              value: count,
              label: {
                normal: {
                  show: count !== 0,
                  color: PRIMARY_COLOR,
                  lineHeight: 14,
                  rich: {},
                },
              },
              labelLine: {
                normal: {
                  show: count !== 0,
                  lineStyle: { color: PRIMARY_COLOR },
                },
              },
              itemStyle: {
                normal: { color: count === 0 ? PRIMARY_COLOR : this.color[index] },
              },
            })),
          },
        ],
      };
    },
  },
  mounted() {
    chart.then(([echarts]) => {
      this.chart = echarts.init(this.$refs.chartRef, { height: 'auto' })
      this.chart.setOption(
        this.genOption(),
      )
    })
  },
}
</script>

<style scoped>
#ring-chart {
  height: inherit;
}
</style>



