<template>
  <div
    id="line-chart"
    ref="chartRef"></div>
</template>

<script>
import chart from '@/etc/echarts'

export default {
  props: {
    dataSource: { type: Object },
  },
  methods: {
    genOption() {
      const { xAxis, yAxis, describe } = this.dataSource
      return {
        title: {
          text: describe,
          left: 'center',
          top: 10,
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: xAxis.data,
          // axisLabel: {
          //   rotate: 20,
          // },
        },
        yAxis: {
          // type: 'value',
          // axisTick: {
          //   show: false,
          // },
          name: yAxis.name,
        },
        tooltip: {
          trigger: 'axis',
        },
        grid: {
          right: 20,
          left: 50,
        },
        dataZoom: [{
          type: 'slider',
          show: true,
          xAxisIndex: 0,
          height: 30,
        }],
        series: [{
          data: yAxis.data,
          type: 'line',
          smooth: true,
          symbol: 'none',
          lineStyle: {
            normal: {
              color: '#f97197',
            },
          },
          areaStyle: {
            normal: {
              color: 'rgba(249, 113, 151, .3)',
            },
          },
        }],
      }
    },
  },
  mounted() {
    chart.then(([echarts]) => {
      this.chart = echarts.init(this.$refs.chartRef, { height: 'auto' })
      this.chart.setOption(this.genOption())
      setTimeout(() => {
        this.chart.resize()
      }, 0)
    })
  },
  watch: {
    dataSource() {
      this.chart.setOption(this.genOption())
      this.chart.resize()
    },
  },
}
</script>

<style scoped>
#line-chart {
  height: inherit;
}
</style>




