<template>
  <div id="chart-box">
    <div class="label">
      <span>{{this.option.name || '---'}}</span>
      <el-tooltip :content="prompt" placement="right" popper-class="tooltip-box">
        <i class="el-icon-info"></i>
      </el-tooltip>
    </div>
    <div class="pie-chart-container" ref="chartRef"></div>
  </div>
</template>

<script>
import chart from '@/etc/echarts'

const DEFAULT_COLOR = '#000'

const genOption = ({ name, color, dataSource }) => ({
  color: [color, '#ddd'],
  series: [
    {
      name,
      type: 'pie',
      avoidLabelOverlap: false,
      silent: true,
      radius: ['80%', '100%'],
      label: {
        normal: {
          show: true,
          position: 'center',
          formatter: ({ percent, dataIndex }) => {
            if (dataIndex === 0) return `${percent.toFixed()}%`
          },
          textStyle: {
            fontSize: 26,
            fontWeight: 'bold',
            color: color || DEFAULT_COLOR,
          },
        },
      },
      labelLine: {
        normal: {
          show: false,
        },
      },
      data: dataSource || [],
    },
  ],
})

export default {
  props: {
    option: { type: Object },
    prompt: { type: String, default: '' },
  },
  watch: {
    option(newVal) {
      this.chart.setOption(
        genOption({ ...newVal }),
      )
    },
  },
  mounted() {
    chart.then(([echarts]) => {
      this.chart = echarts.init(this.$refs.chartRef);
      this.chart.setOption(
        genOption({ ...this.option }),
      );
    })
  },
}

</script>

<style lang="less" scoped>
#chart-box {
  width: 140px;
  .label {
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    height: 60px;
    position: relative;
    line-height: 1.2;
    i {
      font-size: 15px;
      color: rgba(66, 165, 245, 0.8);
    }
  }
  .pie-chart-container {
    width: 100%;
    height: 160px;
  }
}
</style>



