<template>
  <div id="resource-analyze">
    <el-card :shadow="headerLoading ? 'always' : 'never'" body-style="padding: 0;" style="margin-bottom: 20px; border: none">
      <div class="header-container" v-loading="headerLoading" element-loading-text="数据加载中">
        <template v-if="!headerLoading">
          <el-card style="width: 30%" body-style="height: 100%;">
            <ring-chart v-bind="headerData.total" />
          </el-card>
          <el-card style="width: 70%; margin-left: 20px;" body-style="height: 100%;">
            <line-chart :dataSource="headerData.lastWeekUpload" />
          </el-card>
        </template>
      </div>
    </el-card>
    <div class="content-container">
      <sidebar mode="resource" :getData="getData" :canExport="canExport"></sidebar>
      <div class="content">
        <el-card v-loading="increasementLoading">
          <div class="increasement">
            <div class="title">新增校本资源量: {{increasementData.total}}</div>
            <div class="detail-container">
              <div v-for="{kind, count} in increasementData.kinds" :key="kind">
                <div>
                  {{usageMap[kind] || kind}}
                </div>
                <div>{{count}}</div>
              </div>
            </div>
          </div>
        </el-card>
        <el-button-group style="margin: 30px 0 10px">
          <el-button @click="buttonType = 'teacher'" :type="buttonType === 'teacher' ? 'primary' : ''">教师</el-button>
          <el-button @click="buttonType = 'date'" :disabled="params.timeType !== 1 && params.timeType !== 2" :type="buttonType === 'date' ? 'primary' : ''">日期</el-button>
        </el-button-group>
        <el-card class="increasement-detail">
          <div>
            <template v-if="buttonType === 'teacher'">
              <el-table :data="dataFormat(teacherData.teachers)" v-loading="detailDataLoading">
                <el-table-column v-for="{key, value} in teacherColumns" :key="key" :prop="key" :label="value" align="center" v-bind="key === 'kind7' ? {'min-width':135} : null" />
              </el-table>
              <el-pagination layout="prev, pager, next" :page-count="teacherData.totalPage || 1" @current-change="handleCurrentChange"></el-pagination>
            </template>
            <template v-else>
              <el-table :data="dataFormat(dateDetailData.dateUpload)" v-loading="detailDataLoading">
                <el-table-column v-for="{key, value} in dateColumns" :key="key" :prop="key" :label="value" align="center" v-bind="key === 'kind7' ? {'min-width':135} : null" />
              </el-table>
            </template>
          </div>
        </el-card>
      </div>
    </div>
  </div>
</template>

<script>
import { DOC_USAGE_NAME_MAP as USAGE_MAP, SUBJECTS, GRADES } from '@/assets/js/consts/common'
import { teacherColumns, dateColumns } from './map.json'
import Sidebar from './../sidebar'
import RingChart from './ring-chart'
import LineChart from './line-chart'
import autoScrollMixin from './autoscroll-minix'

const usageMapFormat = (usageMap) => Object.entries(usageMap).slice(1).map(([key, value]) => ({ key: `kind${key}`, value }))
const defaultIncreasementData = {
  total: 0,
  kinds: '1234567'.split('').map(ele => ({ kind: parseInt(ele), count: 0 })),
}
const DOC_USAGE_NAME_MAP = { ...USAGE_MAP, 7: '试题组和互动资料' }

const PAGE_SIZE = 20

export default {
  mixins: [autoScrollMixin],
  components: { Sidebar, RingChart, LineChart },
  data() {
    return {
      usageMap: DOC_USAGE_NAME_MAP,
      teacherColumns: [...teacherColumns, ...usageMapFormat(DOC_USAGE_NAME_MAP)],
      dateColumns: [...dateColumns, ...usageMapFormat(DOC_USAGE_NAME_MAP)],
      buttonType: 'teacher',
      headerLoading: true,
      increasementLoading: true,
      detailDataLoading: true,
      headerData: {},
      teacherData: {},
      dateDetailData: {},
      increasementData: defaultIncreasementData,
      params: {},
    }
  },
  methods: {
    getData(params) {
      this.params = params
      if (params.timeType === 3) this.resetButtonType()
      this.getIncreasementOverall(params)
      this.getIncreasementDetail({ ...params, page: 1, limit: PAGE_SIZE })
    },
    getSummary() {
      this.$req.get('/teacher/api/data/resource/summary').then(({ data }) => {
        this.headerData = data
      }).catch(() => {
        this.$message({ type: 'error', message: '请求资源概览数据出错' })
      }).finally(() => {
        this.headerLoading = false
      })
    },
    getIncreasementOverall(params) {
      this.increasementLoading = true
      this.$req.get('/teacher/api/data/resource/increasement', {
        params,
      }).then(({ data }) => {
        if (!data.total) this.$noDataNotice()
        this.increasementData = data
      }).catch(() => {
        this.$message({ type: 'error', message: '请求新增资源量数据出错' })
      }).finally(() => {
        this.increasementLoading = false
      })
    },
    getIncreasementDetail(params) {
      this.detailDataLoading = true
      const { timeType } = params
      Promise.all([
        this.$req.get('/teacher/api/data/resource/teacher', { params }),
        timeType === 1 || timeType === 2
          ? this.$req.get('/teacher/api/data/resource/date_increasement', { params })
          : {},
      ]).then(([{ data: teacherData }, { data: dateDetailData }]) => {
        this.teacherData = teacherData
        this.dateDetailData = dateDetailData || {}
      }).catch(() => {
        this.$message({ type: 'error', message: '请求资源详细数据出错' })
      }).finally(() => {
        this.detailDataLoading = false
      })
    },
    handleCurrentChange(page) {
      window.scrollTo(0, document.querySelector('.increasement-detail').offsetTop - 20)
      this.getIncreasementDetail({ ...this.params, page, limit: PAGE_SIZE })
    },
    dataFormat(data) {
      if (data === undefined) return []
      return data.map(ele => {
        const tmp = ele.kinds.reduce((acc, { count, kind }) => ({ ...acc, [`kind${kind}`]: count }), {})
        const { subject, grade } = ele
        if ('subject' in ele) ele.subject = SUBJECTS[subject] || subject
        if ('grade' in ele) ele.grade = GRADES[grade] || grade
        return { ...ele, ...tmp }
      })
    },
    resetButtonType() {
      this.buttonType = 'teacher'
    },
  },
  computed: {
    canExport() {
      const { detailDataLoading, teacherData: { teachers }, dateDetailData: { dateUpload } } = this
      return !detailDataLoading && !!((teachers && teachers.length) || (dateUpload && dateUpload.length))
    },
  },
  mounted() {
    this.getSummary()
  },
}
</script>

<style lang="less" scoped>
#resource-analyze {
  .header-container {
    display: flex;
    height: 25vh;
    min-height: 250px;
    position: relative;
  }
  .content-container {
    display: flex;
    .content {
      width: calc(~'100% - 320px');
      flex-grow: 1;
      margin-left: 20px;
      .increasement {
        .title {
          font-size: 20px;
          font-weight: bold;
          margin-bottom: 20px;
        }
        .detail-container {
          display: flex;
          justify-content: space-between;
          > div {
            width: 14.2857%;
            border-radius: 8px;
            text-align: center;
            height: 95px;
            background-size: 100% 100%;
            color: #fff;
            font-size: 18px;
            padding: 5px 0;
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;
            &:not(:first-child) {
              margin-left: 30px;
            }
            &:nth-child(3n + 1) {
              background: #1fb9fe no-repeat
                url('~assets/img/bg-transparent.png');
            }
            &:nth-child(3n + 2) {
              background: #f9ad71 no-repeat
                url('~assets/img/bg-transparent.png');
            }
            &:nth-child(3n + 3) {
              background: #37ce9a no-repeat
                url('~assets/img/bg-transparent.png');
            }
            > div {
              &:last-child {
                font-size: 22px;
              }
            }
            i {
              font-size: 16px;
            }
          }
        }
      }
    }
    .el-pagination {
      display: flex;
      justify-content: center;
      margin-top: 20px;
    }
  }
}
</style>




