<template>
  <el-card
    v-if="visible"
    v-loading="loading"
    class="school-info"
    body-style="width: inherit; display: flex; justify-content: space-around;">
    <div
      v-for="{label, value} in overallData"
      :key="label"
      class="item">
      <span>{{label}}</span>
      <span>{{value}}</span>
    </div>
  </el-card>
</template>

<script>
import { mapState } from 'vuex'
import { personResourceMap } from './map.json'

export default {
  data() {
    return {
      overallData: new Array(4).fill({ value: '0', label: '--' }),
      loading: true,
    }
  },
  methods: {
    getProfileData() {
      if (!this.visible) return
      const { teacher: { isManager } } = this
      const prefix = isManager ? '全校' : '全年级'
      this.$req('/teacher/api/data/course/profile').then(({ data }) => {
        const { profile } = data
        this.overallData = Object.entries(profile)
          .map(([key, value]) => ({ value, label: `${prefix}${personResourceMap[key]}` || key }))
      }).catch(() => {
        this.$message({ type: 'error', message: '请求概览数据出错' })
      }).finally(() => {
        this.loading = false
      })
    },
  },
  mounted() {
    this.getProfileData()
  },
  computed: {
    visible() {
      const { teacher = {} } = this
      const { isManager, isSubjectMaster } = teacher
      return isManager || isSubjectMaster
    },
    ...mapState(['teacher']),
  },
}
</script>

<style lang="less" scoped>
.school-info {
  margin-bottom: 20px;
  .item {
    height: 100px;
    width: 15%;
    background: no-repeat url("~assets/img/bg-transparent.png");
    background-size: 100% 100%;
    border-radius: 5px;
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    font-size: 18px;
    > span {
      &:last-child {
        font-size: 22px;
      }
    }
    &:nth-child(1) {
      background-color: #00b8ff;
    }
    &:nth-child(2) {
      background-color: #f9ad71;
    }
    &:nth-child(3) {
      background-color: #f97184;
    }
    &:nth-child(4) {
      background-color: #2bcf99;
    }
  }
}
</style>




