<template>
  <el-tabs :value="$route.meta.activeName" @tab-click="clickHandle" style="width:100%">
    <el-tab-pane v-for="({content, name}, index) in columns" :label="content" :key="index" :name="name"></el-tab-pane>
  </el-tabs>
</template>

<script>
import { mapState } from 'vuex'

export default {
  methods: {
    clickHandle(tab) {
      this.$router.push(`/${tab.name}`)
    },
  },
  computed: {
    columns() {
      // temp: [[Map, Function]]
      const temp = [
        [{ content: '老师分析', name: 'teacher-analyze' }, ({ isSubjectMaster, isManager }) => isSubjectMaster || isManager],
        [{ content: '学生分析', name: 'student-analyze' }, ({ isCourseTeacher }) => isCourseTeacher],
        [{ content: '班级分析', name: 'class-analyze' }, () => true],
        [{ content: '校本资源分析', name: 'resource-analyze' }, ({ isSubjectMaster, isManager }) => isSubjectMaster || isManager],
      ]
      return temp
        .filter(([, condition]) => condition(this.teacher || {}))
        .map(([value]) => value)
    },
    ...mapState({ teacher: 'teacher' }),
  },
  watch: {
    columns(val) {
      if (val.length && this.$route.path === '/') {
        this.$router.push(`/${val[0].name}`)
      }
    },
  },

}
</script>

<style lang="less">
.el-tabs__nav-scroll {
  display: flex;
  justify-content: center;
}
.el-tabs__content {
  display: none;
}
.el-tabs__header {
  margin-bottom: 0;
}
#tab-student-analyze {
  outline: none;
  border: none;
  border-color: transparent;
  outline-color: transparent;
  &:focus {
    outline: none;
    border: none;
    border-color: transparent;
    outline-color: transparent;
  }
}
</style>



