<template>
  <el-card>
    <div id="paging-table">
      <el-table :data="dataSource">
        <el-table-column v-for="({key, value}, index) in columns" :key="index" :prop="key" :label="value" align="center"></el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button type="text" size="mini" @click="toStudentDetail(scope)">详情</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination v-if="dataSource.length" @current-change="currentChangeHandle" :total="total" :page-size="20" layout="prev, pager, next"></el-pagination>
    </div>
  </el-card>
</template>

<script>
export default {
  props: {
    dataSource: { type: Array, default: () => [] },
    columns: { type: Array, default: () => [] },
    total: { type: Number, default: 0 },
  },
  data() {
    return {

    }
  },
  methods: {
    currentChangeHandle(val) {
      console.log(val)
    },
    toStudentDetail({ row: { id } }) {
      console.log(id)
      this.$router.push('/student-analyze/detail')
    },
  },
}
</script>

<style lang="less">
#paging-table {
  display: flex;
  align-items: center;
  flex-direction: column;
  .el-pagination {
    margin-top: 20px;
  }
}
</style>




