<template>
  <div class="class-analyze">
    <overall />
    <div>
      <sidebar mode="course" :getData="getData"></sidebar>
      <div class="content" v-loading="loading" element-loading-text="数据加载中">
        <template v-if="init">
          <div class="chart-container">
            <el-card
              class="line-chart"
              body-style="height: 100%;">
              <div class="select-bar">
                <el-button-group>
                  <el-button
                    v-for="({key, label}, index) in buttons"
                    :key="key"
                    :type="selectIndex === index ? 'primary' : ''"
                    @click="selectIndex = index">
                    {{label}}
                  </el-button>
                </el-button-group>
                <div v-if="selectIndex === 3">
                  <span>优秀学生判定标准: 正确率 > <strong>80%</strong></span>
                </div>
              </div>
              <line-chart
                :dataSource="lineChartData"
                style="height: calc(100% - 40px);"></line-chart>
            </el-card>
            <el-card
              class="radar-chart"
              body-style="height: 100%;"
              style="margin-left: 20px"
              v-if=" radarShow">
              <radar style="height: inherit;" :dataSource="classCmRadar" v-if="classCmRadar"></radar>
            </el-card>
          </div>
          <analyze-tag
            v-if="returnType === 4 || returnType === 2"
            :dataSource="weakKnowledges"
            style="margin-top: 20px" />
          <div class="title" ref="titleRef">{{ `${selectDescribe}情况` }}</div>
          <el-card>
            <el-table :data="tableOption.data" size="medium">
              <el-table-column
                v-for="{key, value} in tableOption.columns"
                :key="key"
                :label="value"
                :prop="key"
                align="center" />
            </el-table>
            <el-pagination
              v-if="returnType === 4"
              style="margin-top: 15px; display:flex; justify-content: center"
              v-bind="paginationOptions"
              :page-count="courseMaterials.totalPage"
              @current-change="handleCurrentChange" />
          </el-card>
        </template>
        <div v-else class="no-data">
          没有数据
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Sidebar from './../sidebar'
import LineChart from './line-chart'
import Overall from './overall'
import Radar from './radar'
import AnalyzeTag from './../analyze-tag'
import { homeworkOverallColumns, homeworkColumns } from './map.json'

const buttons = [
  { label: '出题量', key: 'questionCount' },
  { label: '正确率', key: 'correctRate' },
  { label: '提交率', key: 'cmSubmitRate' },
  { label: '优秀学生数', key: 'eliteStudent' },
]

export default {
  components: { Sidebar, LineChart, AnalyzeTag, Radar, Overall },
  data() {
    return {
      homeworkOverallColumns,
      homeworkColumns,
      buttons,
      loading: true,
      radarShow: false,
      selectIndex: 0,
      weakKnowledges: [],
      classCmRadar: [],
      summary: [],
      courseMaterials: {
        data: [],
        totalPage: 0,
      },
      paginationOptions: {
        currentPage: 1,
        pageSize: 20,
        layout: 'prev,pager,next',
      },
      returnType: 1,
      params: {},
      init: false,
    }
  },
  methods: {
    getData(params, { type, selectDescribe, empty }) {
      if (empty) {
        this.loading = false
        return
      }
      this.radarShow = ('course' in params || 'class' in params)
      if (selectDescribe) this.selectDescribe = selectDescribe
      this.returnType = type
      this.params = params
      this.loading = true
      this.selectIndex = 0
      if (type === 4 && params.limit === undefined) {
        params.limit = this.paginationOptions.pageSize
        params.start = 0
      }
      this.$req('/teacher/api/data/course/summary', { params }).then(({ data }) => {
        const { weakKnowledges, summary = [], classCmRadar, courseMaterials } = data
        this.weakKnowledges = weakKnowledges.map(({ name }) => name)
        this.classCmRadar = classCmRadar
        this.summary = summary.map(ele => ({ ...ele, cmAverageSecond: Math.ceil(ele.cmAverageSecond / 60) }))
        this.courseMaterials = {
          data: courseMaterials.data.map(ele => ({ ...ele, cmAverageSecond: Math.ceil(ele.cmAverageSecond / 60) })),
          totalPage: courseMaterials.totalPage,
        }
      }).finally(() => {
        this.init = true
        this.loading = false
      })
    },
    handleCurrentChange(pageIndex) {
      const { titleRef } = this.$refs
      window.scrollTo(0, titleRef.offsetTop - 20)
      const { pageSize: limit } = this.paginationOptions
      this.getData({ ...this.params, limit, start: (pageIndex - 1) * limit }, { type: 4 })
    },
  },
  computed: {
    lineChartData() {
      const { tableOption: { data }, selectDescribe, returnType } = this
      const { key, label } = buttons[this.selectIndex]
      return {
        xAxis: {
          data: data.map(ele => ele.name),
        },
        yAxis: {
          name: label,
          data: data.map(ele => (returnType === 4 && key === 'eliteStudent' ? ele[key].split('/')[0] : ele[key])),
        },
        describe: `${selectDescribe}${label}分布图`,
      }
    },
    tableOption() {
      const { returnType, courseMaterials, summary } = this
      return {
        data: returnType === 4 ? courseMaterials.data : summary,
        columns: returnType === 4 ? homeworkColumns : homeworkOverallColumns,
      }
    },
  },
  mounted() {
    // this.getData()
  },
}
</script>


<style lang="less" scoped>
.class-analyze {
  display: flex;
  flex-direction: column;
  > div {
    width: 100%;
    display: flex;
  }
  .content {
    width: 100%;
    width: ~"calc(100% - 260px)";
    // padding-left: 20px;
    margin-left: 20px;
    .chart-container {
      width: 100%;
      height: 380px;
      display: flex;
      .line-chart {
        width: 70%;
        .select-bar {
          display: flex;
          justify-content: space-between;
          align-items: center;
          strong {
            font-size: 16px;
          }
        }
      }
      .radar-chart {
        width: 30%;
      }
      .line-chart,
      .radar-chart {
        flex-grow: 1;
      }
    }
    .title {
      font-size: 20px;
      font-weight: bold;
      margin: 20px 0 10px;
    }
    .no-data {
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 18px;
    }
  }
}
</style>



