<template>
  <div v-loading="loading" id="content-container" element-loading-text="数据加载中">
    <div class="subjectSelector">
      <span>教学班级:</span>
      <el-select v-model="subjectValue" placeholder="请选择" size="medium">
        <el-option v-for="{label, value} in subjectList" :key="value" :label="label" :value="value">
        </el-option>
      </el-select>
    </div>
    <div class="title">{{name}}的上周 ({{prevWeek}}) 学习报告</div>
    <template v-if="loading === false">
      <div class="row-1">
        <prob-chart :dataSource="collection.probChart"></prob-chart>
        <num-card :dataSource="collection.numCard" style="margin: 0 20px"></num-card>
        <bar-chart v-bind="collection.barChart" height="260px" style="min-width: 600px"></bar-chart>
      </div>
      <template v-if="subjectValue === 0">
        <radar-chart :dataSource="collection.randarChart" :name="name" style="margin-bottom: 20px"></radar-chart>
      </template>
      <template v-else>
        <broken-line-chart :dataSource="collection.brokenLine" style="margin-bottom: 20px"></broken-line-chart>
        <analyze-tag :dataSource="collection.analyzeTag" style="margin-bottom: 20px"></analyze-tag>
      </template>
      <sort-table v-bind="collection.sortTable"></sort-table>
    </template>
  </div>
</template>

<script>
import axios from 'axios'
import { mapState } from 'vuex'
import dayjs from 'dayjs'

import { SUBJECTS } from '@/assets/js/consts/common'
import ProbChart from './../prob-chart'
import NumCard from './../num-card'
import BarChart from './../bar-chart'
import RadarChart from './../radar-chart'
import SortTable from './../sort-table'
import AnalyzeTag from './../analyze-tag'
import BrokenLineChart from './../broken-line-chart'
import { subjectOverallColumns, subjectDetailColumns } from './map.json'

export default {
  components: { RadarChart, SortTable, AnalyzeTag, BrokenLineChart, ProbChart, NumCard, BarChart },
  data() {
    return {
      loading: true,
      name: '',
      dataSource: {},
      subjectList: [],
      subjectValue: 0,
    }
  },
  computed: {
    collection() {
      const { dataSource } = this
      const { correctRateDistribution, correctRate, finishRate, previewCount,
        questionCount, courseDetail, weakKnowledges, courseMaterials,
      } = dataSource
      const temp = {
        barChart: {
          dataSource: correctRateDistribution,
          studentCorrect: correctRate,
        },
        probChart: [correctRate, finishRate],
        numCard: [previewCount, questionCount],
      }
      if (this.subjectValue === 0) {
        const randarChartData = courseDetail.map(({ subject, correctRate: accuracy }) => ({ subject: this.subjectConvert(subject), accuracy }))
        temp.randarChart = this.genRandarData(randarChartData)
        temp.sortTable = {
          dataSource: courseDetail.map(ele => ({ ...ele, subject: ele.name })),
          columns: subjectOverallColumns,
          notSortProps: ['subject'],
        }
      } else {
        temp.analyzeTag = weakKnowledges
        temp.brokenLine = courseMaterials.map(({ name, correctRate: sAccuracy, avgCorrectRate: cAccuracy }) => ({ name, sAccuracy, cAccuracy }))
        temp.sortTable = {
          dataSource: courseMaterials,
          columns: subjectDetailColumns,
          notSortProps: ['name'],
        }
      }
      return temp
    },
    prevWeek() {
      const date = dayjs().subtract(7, 'day')
      const day = date.day()
      const start = date.subtract(day - 1, 'day').format('YYYY.MM.DD')
      const end = date.add(7 - day, 'day').format('YYYY.MM.DD')
      return `${start}-${end}`
    },
    ...mapState({ accessToken: 'accessToken' }),
  },
  methods: {
    getSubjectOverall(id) {
      const url = `/teacher/api/data/students/${id}/overall`
      this.loading = true
      axios.get(url, {
        headers: { Authorization: this.accessToken },
      }).then(({ data }) => {
        if (!data.hasData) this.$noDataNotice()
        this.dataSource = data
        const subjects = data.courseDetail.map(({ id: value, name: label }) => ({ value, label }))
        this.subjectList = [{ value: 0, label: '所有学科' }, ...subjects]
      }).finally(() => {
        this.loading = false
      })
    },
    getSubjectDetail({ id, courseId }) {
      const url = `/teacher/api/data/students/${id}/${courseId}`
      this.loading = true
      axios.get(url, {
        headers: { Authorization: this.accessToken },
      }).then(({ data }) => {
        if (!data.hasData) this.$noDataNotice()
        // 转为形式一样的对象好作处理
        let temp = data
        temp = { ...temp, ...temp.subjectDetail }
        delete temp.subjectDetail
        this.dataSource = temp
      }).finally(() => {
        this.loading = false
      })
    },
    subjectConvert(value) {
      return SUBJECTS[value] || value
    },
    genRandarData(data) {
      const getAvg = (...param) => {
        const accResult = param.reduce((acc, ele) => acc + ele, 0)
        const avg = (accResult / param.length).toFixed(1)
        return parseFloat(avg[avg.length - 1] === '0' ? avg.split('.')[0] : avg)
      }
      return Object.entries(data.reduce((acc, { subject, accuracy }) => {
        if (subject in acc) {
          return { ...acc, [subject]: [...acc[subject], accuracy] }
        }
        return { ...acc, [subject]: [accuracy] }
      }, {})).map(([subject, accuracy]) => ({ subject, accuracy: getAvg(...accuracy) }))
    },
  },
  watch: {
    subjectValue(newVal) {
      const { id } = this.$route.query
      if (newVal === 0) {
        this.getSubjectOverall(id)
      } else {
        this.getSubjectDetail({
          id, courseId: newVal,
        })
      }
    },
  },
  mounted() {
    this.$nextTick(() => {
      window.setTimeout(() => {
        this.$root.$el.scrollIntoView(1)
      }, 0)
    })
    const { id, name } = this.$route.query
    this.name = name
    if (id === undefined) {
      this.$router.push('/student-analyze')
      return
    }
    this.getSubjectOverall(id)
  },
}
</script>

<style lang="less" scoped>
#content-container {
  min-height: calc(~'100vh - 110px');
  .subjectSelector {
    margin-bottom: 20px;
    span {
      margin-right: 20px;
    }
    .el-select {
      width: 170px;
    }
  }
  .title {
    font-size: 22px;
    font-weight: bold;
    margin-bottom: 20px;
  }
  .row-1 {
    flex-grow: 1;
    display: flex;
    margin-bottom: 20px;
    justify-content: space-between;
    > div {
      &:first-child,
      &:last-child {
        flex-grow: 1;
        max-width: calc(~'50% - 197px');
      }
    }
    .num-box {
      width: 315px;
      height: 135px;
    }
  }
}
</style>




