export default {
  methods: {
    scrollAuto() {
      const autoScroll = () => {
        const { scrollY } = autoScroll
        delete autoScroll.scrollY
        if (window.scrollY <= scrollY) return
        // scrollY在.header-container的offset范围内
        const headerContainer = document.querySelector('.header-container')
        const { offsetTop } = headerContainer
        const { height } = getComputedStyle(headerContainer)
        const topMax = offsetTop + parseInt(height) + 20
        const topMin = offsetTop
        if (topMax > window.scrollY && topMin < window.scrollY) {
          window.scrollTo(0, topMax)
        }
      }
      this.handleScroll = () => {
        window.clearTimeout(autoScroll.timer)
        if (autoScroll.scrollY === undefined) {
          autoScroll.scrollY = window.scrollY
        }
        autoScroll.timer = setTimeout(autoScroll, 50)
      }
      window.addEventListener('scroll', this.handleScroll, false)
    },
  },
  mounted() {
    this.scrollAuto()
  },
  destroyed() {
    window.removeEventListener('scroll', this.handleScroll, false)
  },
}



