<template>
  <div id="teacher-analyze">
    <sidebar mode="teacher" :getData="getData" :canExport="!!dataSource.length && !pagingTableLoading"></sidebar>
    <div class="content">
      <div class="rank-table">
        <el-button-group>
          <el-button :type="selectIndex === 1 ? 'primary' : 'button'" @click="selectIndex = 1">批改次数</el-button>
          <el-button :type="selectIndex === 2 ? 'primary' : 'button'" @click="selectIndex = 2">作业发布</el-button>
        </el-button-group>
        <div class="table-box">
          <el-table size="mini" :data="rankDataConvert(selectIndex === 1 ? cmReviewData : cmPublishData).slice(0,3)" v-loading="rankTableLoading">
            <el-table-column align="center" :prop="prop " :label="label " v-for="({prop, label}, index) in rankColumns " :key="index">
              <template slot-scope="{row}">
                <div class="rank-table-cell">
                  <div :class="['logo', row.rank <= 3 ? `top${row.rank}` : '']" v-if="prop === 'rank'"></div>
                  <span>{{ row[prop] }}</span>
                </div>
              </template>
            </el-table-column>
          </el-table>
          <el-table size="mini" :data="rankDataConvert(selectIndex === 1 ? cmReviewData : cmPublishData).slice(3,6)" v-loading="rankTableLoading">
            <el-table-column align="center" :prop="prop " :label="label " v-for="({prop, label}, index) in rankColumns " :key="index">
              <template slot-scope="{row}">
                <div class="rank-table-cell">
                  <div :class="['logo', row.rank <= 3 ? `top${row.rank}` : '']" v-if="prop === 'rank'"></div>
                  <span>{{ row[prop] }}</span>
                </div>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div class="paging-table">
          <div class="title" ref="title">
            <span>{{pagingTableTitle}}</span>
            <el-tooltip placement="right" popper-class="tooltip-box">
              <div slot="content" v-html="explainText"></div>
              <i v-show="pagingTableTitle.length" class="el-icon-info"></i>
            </el-tooltip>
          </div>
          <el-table :data="pagingDataConvert(dataSource)" v-loading="pagingTableLoading">
            <el-table-column align="center" v-bind="{prop, label}" v-for="({prop, label}) in pagingTableColumns" :key="prop"></el-table-column>
          </el-table>
          <el-pagination v-if="paginationIsShow " :page-size="20" :current-page="currentPage" @current-change="currentChangeHandle" layout="prev, pager, next" :page-count="pageCount"></el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { mapState } from 'vuex'
import { SUBJECTS, GRADES } from '@/assets/js/consts/common'

import Sidebar from './../sidebar'
import columns from './map.json'

const PAGE_SIZE = 20

const explainText = `批改作业次数：教师完成批改含有主观题的互动资料或者试题组的次数，其中完成一个学生的批改算作一次<br>
课堂互动：指点击上课后进行的操作，包括截图直接下发、截图制作答题卡、截图保存板书、随机点名、控屏和同屏的次数的总和<br>
学生人均答题量：下发的试题组和互动资料里题目总量/下发次数<br>
导出：可以查看全部教师的数据情况`

export default {
  components: { Sidebar },
  data() {
    return {
      explainText,
      selectIndex: 1,
      dataSource: [],
      cmReviewData: [],
      cmPublishData: [],
      pagingTableColumns: [],
      pageSize: PAGE_SIZE,
      currentPage: 1,
      pageCount: 0,
      paginationIsShow: true,
      pagingTableLoading: true,
      rankTableLoading: true,
      pagingTableTitle: '',
      params: {},
    }
  },
  methods: {
    setPagingColumns(grade, subject) {
      let temp
      if (subject !== 0) {
        temp = columns.subjectColumns
      } else if (grade !== 0) {
        temp = columns.gradeColumns
      } else {
        temp = columns.allGradeColumns
      }
      this.pagingTableColumns = temp
    },
    currentChangeHandle(page) {
      this.currentPage = page
      this.getPagingData({ ...this.params, page })
    },
    pagingDataConvert(data) {
      return data.map(ele => {
        let temp = { ...ele }
        if ('subject' in ele) temp = { ...temp, subject: SUBJECTS[ele.subject] || ele.subject }
        if ('grade' in ele) temp = { ...temp, grade: GRADES[ele.grade] || ele.grade }
        return temp
      })
    },
    rankDataConvert(data) {
      return data.map((ele, index) => {
        const temp = { ...ele }
        delete temp.reviewCount
        delete temp.publishCount
        return ({
          ...temp,
          rank: index + 1,
          grade: GRADES[ele.grade] || ele.grade,
          subject: SUBJECTS[ele.subject] || ele.subject,
          count: 'reviewCount' in ele ? ele.reviewCount : ele.publishCount,
        })
      })
    },
    getPagingData(params, init) {
      if (!init) {
        const { offsetTop } = this.$refs.title
        window.scrollTo(0, offsetTop)
      }
      this.pagingTableLoading = true
      axios.get('/teacher/api/data/teacher/analysis', {
        headers: { Authorization: this.accessToken },
        params,
      }).then(({ data }) => {
        const findResult = Object.entries(data).find(([, value]) => Array.isArray(value) && value.length)
        this.dataSource = findResult === undefined ? [] : findResult[1]
        this.pageCount = data.totalPage
        this.$nextTick(() => {
          if (!this.dataSource.length) this.$noDataNotice()
        })
      }).finally(() => {
        this.pagingTableLoading = false
      })
    },
    getRankData(params) {
      this.rankTableLoading = true
      axios.get('/teacher/api/data/teacher/overall', {
        headers: { Authorization: this.accessToken },
        params,
      }).then(({ data }) => {
        const { cmReviewData, cmPublishData } = data
        this.cmReviewData = cmReviewData
        this.cmPublishData = cmPublishData
      }).finally(() => {
        this.rankTableLoading = false
      })
    },
    genTitle(grade, subject) {
      if (!grade && !subject) {
        return '所有年级分析'
      } else if (grade && !subject) {
        return `${GRADES[grade] || grade}年级分析`
      } else if (grade && subject) {
        return `${GRADES[grade] || grade}年级${SUBJECTS[subject] || subject}分析`
      } else if (!grade && subject) {
        return `全校${SUBJECTS[subject] || subject}分析`
      }
    },
    getData(params) {
      this.params = params
      this.currentPage = 1
      const { subject, grade } = params
      this.pagingTableTitle = this.genTitle(grade, subject)
      this.setPagingColumns(grade, subject)
      this.paginationIsShow = !!subject && this.dataSource.length
      this.getPagingData({ ...params, page: !subject ? 0 : 1 }, true)
      this.getRankData(params)
    },
  },
  computed: {
    rankColumns() {
      const { selectIndex } = this
      if (selectIndex === 1) return [...columns.rankTableColumns, { prop: 'count', label: '批改次数' }]
      if (selectIndex === 2) return [...columns.rankTableColumns, { prop: 'count', label: '作业发布' }]
    },
    ...mapState({ accessToken: 'accessToken' }),
  },
}
</script>

<style lang="less" scoped>
#teacher-analyze {
  display: flex;
  .content {
    width: 100%;
    flex-shrink: 1;
    margin-left: 20px;
    min-height: calc(~'100vh - 160px');
    .top1 {
      background-image: url('~assets/img/top1.png');
    }
    .top2 {
      background-image: url('~assets/img/top2.png');
    }
    .top3 {
      background-image: url('~assets/img/top3.png');
    }
    .logo {
      background-repeat: no-repeat;
      height: 31px;
      width: 22px;
      margin-left: -31px;
      + span {
        margin-left: 10px;
      }
    }
    .rank-table-cell {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  .el-table {
    border: 1px solid rgb(235, 238, 245);
    border-radius: 3px;
    margin-top: 20px;
  }
  .paging-table {
    display: flex;
    flex-direction: column;
    align-items: center;
    .title {
      font-size: 20px;
      font-weight: bold;
      align-self: flex-start;
      margin-top: 20px;
      padding-top: 20px;
      i {
        color: rgba(0, 0, 0, 0.5);
        font-size: 0.9em;
      }
    }
    > div {
      width: 100%;
    }
    > .el-pagination {
      margin-top: 20px;
      display: flex;
      justify-content: center;
    }
    table {
      thead {
        width: inherit;
        > tr {
          width: inherit;
          display: flex;
          flex-grow: 1;
        }
      }
    }
  }
  .table-box {
    display: flex;
    align-items: flex-start;
    .el-table {
      flex-grow: 1;
      width: auto !important;
      &:last-child {
        margin-left: 20px;
      }
    }
  }
}
</style>



