<template>
  <el-card>
    <el-table :data="dataSource" :sort-default="{prop: 'accuracy'}">
      <el-table-column :sortable="findProp(notSortProps, key)" v-for="({key, label}) in columns" :key="key" :label="label" :prop="key" align="center"></el-table-column>
    </el-table>
  </el-card>
</template>

<script>
export default {
  props: {
    dataSource: { type: Array, default: () => [] },
    columns: { type: Array, default: () => [] },
    notSortProps: { type: Array, default: () => [] },
  },
  methods: {
    findProp(notSortProps, key) {
      return !notSortProps.find(ele => ele === key)
    },
  },
}
</script>



