<template>
  <div id="line-chart" ref="chartRef"></div>
</template>

<script>
import chart from '@/etc/echarts'
import dayjs from 'dayjs'

export default {
  props: {
    dataSource: {
      type: Array, default: () => [],
    },
  },
  methods: {
    getPrevWeekDuration() {
      const prevWeek = dayjs().subtract(1, 'week')
      // 从周一开始
      const start = prevWeek.startOf('week').add(1, 'day').format('YYYY/MM/DD')
      const end = prevWeek.endOf('week').add(1, 'day').format('YYYY/MM/DD')
      return `${start}-${end}`
    },
    genOption() {
      return {
        title: {
          text: `上周(${this.getPrevWeekDuration()})新增校本资源量折线图`,
          left: 'center',
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: '周一,周二,周三,周四,周五,周六,周日'.split(','),
          axisTick: {
            show: false,
          },
        },
        yAxis: {
          type: 'value',
          axisTick: {
            show: false,
          },
        },
        grid: {
          bottom: 30,
        },
        series: [{
          data: this.dataSource,
          type: 'line',
          smooth: true,
          itemStyle: {
            normal: {
              opacity: 0,
            },
          },
          lineStyle: {
            normal: {
              color: '#f97197',
            },
          },
          areaStyle: {
            normal: {
              color: 'rgba(249, 113, 151, .3)',
            },
          },
        }],
      }
    },
  },
  mounted() {
    chart.then(([echarts]) => {
      this.chart = echarts.init(this.$refs.chartRef, { height: 'auto' })
      this.chart.setOption(this.genOption())
      setTimeout(() => {
        this.chart.resize()
      }, 0)
    })
  },
}
</script>

<style scoped>
#line-chart {
  height: inherit;
}
</style>




