<template>
  <div id="content-container">
    <sidebar :getData="getStudentOverall" mode="student"></sidebar>
    <div id="student-analyze" v-loading="loading" element-loading-text="数据加载中">
      <template v-if="loading === false && !empty">
        <div class="row-1">
          <prob-chart :dataSource="collection.probChart" style="width: 30%; flex: 1"></prob-chart>
          <num-card :dataSource="collection.numCard" style="margin: 0 20px; flex: 1; max-width: 315px"></num-card>
          <rank-table :dataSource="collection.rankTable" style="flex: 1" :columns="rankColumns"></rank-table>
        </div>
        <bar-chart :dataSource="collection.barChart" height="500px"></bar-chart>
        <el-card v-if="course !== 0" style="margin-top: 20px">
          <el-table :data="collection.pagingTable">
            <el-table-column v-for="({key, value}, index) in studentColumns" :key="index" :prop="key" :label="value" align="center"></el-table-column>
            <el-table-column label="" align="center">
              <template slot-scope="scope">
                <el-button type="text" size="mini" @click="toStudentDetail(scope)">详情</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-card>
      </template>
      <div class="no-data" v-else>
        没有数据
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import axios from 'axios'
import { intervalTimeOptions, rankColumns, studentColumns } from './map.json'
import Sidebar from './../sidebar'
import NumCard from './../num-card'
import BarChart from './../bar-chart'
import PagingTable from './../paging-table'
import RankTable from './../rank-table'
import ProbChart from './../prob-chart'


export default {
  name: 'student-analyze',
  components: { NumCard, BarChart, PagingTable, RankTable, ProbChart, Sidebar },
  data() {
    return {
      loading: true,
      course: '',
      courseType: '',
      intervalTimeOptions,
      studentColumns,
      rankColumns,
      dataSource: {},
      empty: false,
    }
  },
  computed: {
    collection() {
      const { dataSource } = this
      const { students, avgCorrectRate, avgFinishRate, correctRateDistribution, previewCount, questionCount } = dataSource
      return {
        rankTable: students.sort((prev, next) => next.correctRate - prev.correctRate).slice(0.10).map((ele, index) => ({ ...ele, rank: index + 1 })),
        probChart: [avgCorrectRate, avgFinishRate],
        barChart: correctRateDistribution,
        numCard: [previewCount, questionCount],
        pagingTable: students,
      }
    },
    ...mapState({ accessToken: 'accessToken' }),
  },
  methods: {
    getStudentOverall(params, { empty }) {
      this.empty = empty
      if (empty) {
        this.loading = false
        return
      }
      this.course = params.course
      this.courseType = params.courseType
      this.loading = true
      axios.get('/teacher/api/data/student/overall', {
        headers: { Authorization: this.accessToken },
        params,
      }).then(({ data }) => {
        if (!data.hasData) this.$noDataNotice()
        this.dataSource = data
      }).finally(() => {
        this.loading = false
      })
    },
    toStudentDetail({ row }) {
      this.$router.push({
        path: '/student-analyze/detail',
        query: {
          id: row.id,
          name: row.name,
          course: this.course,
          courseType: this.courseType,
        },
      })
    },
  },
}
</script>

<style lang="less" scoped>
#content-container {
  display: flex;
  background: transparent;
  #student-analyze {
    min-height: calc(~"100vh - 170px");
    width: calc(~"100% - 320px");
    flex-grow: 1;
    margin-left: 20px;
    > .el-card {
      &:last-child {
        margin-bottom: 0;
      }
    }
    .row-1 {
      flex-grow: 1;
      display: flex;
      margin-bottom: 20px;
      justify-content: space-between;
      > div {
        &:first-child,
        &:last-child {
          flex-grow: 1;
        }
      }
      .num-box {
        width: 315px;
        height: 135px;
      }
    }
  }
  .no-data {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
  }
}
</style>




