import axios, { defaults } from 'axios';

import Vue from 'vue'
import VueRouter from 'vue-router'
import Vuex from 'vuex'
import Navbar from 'components/navbar'
import Raven from 'raven-js'
import RavenVue from 'raven-js/plugins/vue'

import {
  Pagination, Message, Dialog, Tabs, TabPane, Menu, MenuItem, Select, Option, Button,
  Card, Table, TableColumn, Tag, DatePicker, Loading, Tooltip, Form, FormItem, ButtonGroup,
  OptionGroup,
} from 'element-ui'
import '../../theme/element-variables.scss'

import addGlobalMethods from './global-methods/'

import './index.less'

import SecondaryBar from './secondary-bar'

import StudentAnalyze from './student-analyze/'
import TeacherAnalyze from './teacher-analyze/'
import StudentDetail from './student-analyze/student-detail'
import ResourceAnalyze from './resource-analyze'
import ClassAnalyze from './class-analyze/'

window.Promise = require('bluebird')

Vue.use(Pagination)
Vue.use(Dialog)
Vue.use(Tabs)
Vue.use(TabPane)
Vue.use(Menu)
Vue.use(MenuItem)
Vue.use(Select)
Vue.use(Option)
Vue.use(Button)
Vue.use(Card)
Vue.use(Table)
Vue.use(TableColumn)
Vue.use(Tag)
Vue.use(DatePicker)
Vue.use(Tooltip)
Vue.use(Form)
Vue.use(FormItem)
Vue.use(ButtonGroup)
Vue.use(OptionGroup)
Vue.use(Loading.directive)
Vue.prototype.$message = Message

Vue.config.productionTip = false

addGlobalMethods(Vue)

defaults.headers.common.X_REQUESTED_WITH = 'XMLHttpRequest'
defaults.xsrfHeaderName = 'X-CSRFTOKEN'
defaults.xsrfCookieName = 'csrftoken'

Vue.prototype.$raven = Raven
if (process.env.NODE_ENV === 'production') {
  Raven.config('http://216e3d581cd9424fad7f5d8c046b2f64@sentry.xdf100.com/6').addPlugin(RavenVue, Vue).install()
}

Vue.use(Vuex)
Vue.use(VueRouter)

const customAxios = (headers) => axios.create({ headers })

const router = new VueRouter({
  mode: 'history',
  base: process.env.NODE_ENV === 'production' ? '/pad/data-analyze' : '/static/w/pages/data-analyze.html',
  routes: [
    { path: '/student-analyze', component: StudentAnalyze, meta: { activeName: 'student-analyze' } },
    { path: '/teacher-analyze', component: TeacherAnalyze, meta: { activeName: 'teacher-analyze' } },
    { path: '/student-analyze/detail', component: StudentDetail, meta: { activeName: 'student-analyze' } },
    { path: '/resource-analyze', component: ResourceAnalyze, meta: { activeName: 'resource-analyze' } },
    { path: '/class-analyze', component: ClassAnalyze, meta: { activeName: 'class-analyze' } },
    // { path: '*', redirect: '/student-analyze' },
  ],
  linkActiveClass: 'active',
})

const store = new Vuex.Store({
  state: {
    teacher: null,
  },
  getters: {
    school: (state) => (state.teacher ? state.teacher.school : null),
    classes: state => (state.teacher ? state.teacher.classes : []),
  },
  mutations: {
    updateTeacher(state, payload) {
      state.teacher = payload.teacher
    },
    updateAccessToken(state, payload) {
      state.accessToken = payload.accessToken
    },
  },
})

router.beforeEach((to, from, next) => {
  if (!store.state.teacher) {
    axios.get('/pad/j/teacher_info').then(response => {
      const { teacher, accessToken } = response.data
      store.commit('updateAccessToken', { accessToken: `Bearer ${accessToken}` })
      Vue.prototype.$req = customAxios({ Authorization: `Bearer ${accessToken}` })
      store.commit('updateTeacher', { teacher })
      if (process.env.NODE_ENV === 'production') {
        Raven.setUserContext({
          username: teacher.name,
          id: teacher.id,
        })
      }
      next()
    })
  } else {
    next()
  }
});

(async () => {
  process.env.NODE_ENV === 'development' && await import(/* webpackChunkName: "mock" */ '@/mock')
  new Vue({
    delimiters: ['${', '}'],
    el: '#app',
    store,
    router,
    data: {},
    components: { Navbar, SecondaryBar },
  })
})()



