<template>
  <div id="num-card">
    <div v-for="({name, value, toolTipText}, index) in chartData" :key="index">
      <div class="label">
        <span>{{name}}</span>&nbsp;
        <el-tooltip :content="toolTipText" placement="right" popper-class="tooltip-box">
          <i class="el-icon-info"></i>
        </el-tooltip>
      </div>
      <div class="num">{{value}}</div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    dataSource: {
      type: Array,
      default: () => [0, 0],
    },
  },
  computed: {
    chartData() {
      return this.genOptions(this.dataSource)
    },
  },
  methods: {
    genOptions([previewCount, questionCount]) {
      return [{
        name: '自主预习量',
        value: previewCount,
        toolTipText: '周期内，学生查看自主预习文件的数量',
      }, {
        name: '答题数量',
        value: questionCount,
        toolTipText: '老师下发的试题组和互动资料的题目数量',
      }]
    },
  },
}
</script>


<style lang="less" scoped>
#num-card {
  width: 315px;
  // height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  > div {
    height: 134px;
    border-radius: 5px;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    flex-shrink: 0;
    &:first-child {
      background: #1fb9fe no-repeat url('~assets/img/bg-transparent.png');
      background-size: 100% 100%;
    }
    &:last-child {
      background: #37ce9a no-repeat url('~assets/img/bg-transparent.png');
      background-size: 100% 100%;
      margin-top: 10px;
    }
    > div {
      text-align: center;
    }
    .label {
      font-size: 22px;
      display: flex;
      align-items: center;
      justify-content: center;
      line-height: 1.2;
      i {
        font-size: 20px;
        line-height: 1.2;
        color: rgba(255, 255, 255, 0.8);
      }
    }
    .num {
      font-size: 34px;
      font-weight: bold;
    }
  }
}
</style>



