<template>
  <el-card style="height: 100%; flex-shrink: 0" v-loading="loading" element-loading-text="数据加载中">
    <div class="sidebar">
      <div class="filter">
        <el-select v-model="timeType" placeholder="请选择">
          <el-option v-for="{value, label} in intervalTimeOptions" :key="value" :label="label" :value="value"></el-option>
        </el-select>
      </div>
      <div class="filter">
        <span>时间筛选</span>
        <el-date-picker v-if="timeType === 1" v-model="time" type="year" placeholder="请选择年" :picker-options="yearPickerOptions" format="yyyy年" :clearable="false"></el-date-picker>
        <el-date-picker v-if="timeType === 2" v-model="time" type="month" placeholder="请选择月" :picker-options="monthPickerOptions" format="yyyy年MM月" :clearable="false"></el-date-picker>
        <el-date-picker v-if="timeType === 3" v-model="time" type="week" placeholder="请选择周" :picker-options="weekPickerOptions" format="yyyy年第WW周" :clearable="false"></el-date-picker>
      </div>
      <div class="describe">
        <div>筛选日期为: </div>
        <div>{{timeSolt}}</div>
      </div>
      <div class="filter">
        <span>内容筛选</span>
        <el-select v-model="selectGrade" placeholder="请选择" :disabled="metaDataEmpty">
          <el-option v-for="item in grades" :key="item.grade" :label="gradeConvert(item.grade)" :value="item.grade"></el-option>
        </el-select>
        <el-form :model="form" v-if="mode === 'course'" :disabled="metaDataEmpty">
          <el-form-item>
            <el-select v-model="form.selectClass" placeholder="请选择" value-key="tag">
              <el-option
                v-for="item in classes"
                :key="item.tag"
                :label="item.name"
                :value="item" />
            </el-select>
          </el-form-item>
          <el-form-item v-if="form.selectClass && form.selectClass.id === 0">
            <el-select v-model="form.subject" placeholder="请选择">
              <el-option
                v-for="{value, label} in subjects"
                :key="value"
                :label="label"
                :value="value" />
            </el-select>
          </el-form-item>
          <el-form-item v-if="form.selectClass && form.selectClass.type === 'class' && form.selectClass.id !== 0">
            <el-select v-model="form.course" placeholder="请选择">
              <el-option
                v-for="{id, name} in form.selectClass.courses"
                :key="id"
                :label="name"
                :value="id" />
            </el-select>
          </el-form-item>
        </el-form>
        <el-select v-if="mode === 'student'" v-model="selectCourse" placeholder="请选择" :disabled="metaDataEmpty">
          <el-option v-for="item in courses" :key="item.id" :label="item.name" :value="item.id"></el-option>
        </el-select>
        <el-select v-if="mode === 'teacher' || mode === 'resource'" v-model="selectSubject" placeholder="请选择">
          <el-option v-for="item in subjects" :key="item.id" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div>
      <div class="btn-container">
        <el-button type="primary" size="medium" @click="submit" :disabled="metaDataEmpty">确定</el-button>
        <a :href="exportUrl">
          <el-button :type="canExport ? 'primary' : 'info'" size="medium" :disabled="!canExport ? true : metaDataEmpty">导出</el-button>
        </a>
      </div>
    </div>
  </el-card>
</template>

<script>
import { mapState } from 'vuex'
import dayjs from 'dayjs'
import qs from 'qs'
import weekOfYear from 'dayjs/plugin/weekOfYear'
import { GRADES, SUBJECTS } from '@/assets/js/consts/common'
import { intervalTimeOptions } from './map.json'

dayjs.extend(weekOfYear)

const nowDate = dayjs()
const exportBaseUrlMap = {
  teacher: '/pad/data/teacher/export',
  student: '/pad/data/student/export',
  resource: '/pad/data/resource/export',
  course: '/pad/data/course/export',
}
const metaDataUrlMap = {
  student: '/teacher/api/data/student/meta',
  teacher: '/teacher/api/data/teacher/meta',
  resource: '/teacher/api/data/resource/meta',
}

const dateConvert = (date) => (date.day() === 0 ? date.add(1, 'day') : date)

export default {
  props: {
    getData: Function,
    mode: { type: String, required: true },
    canExport: { type: Boolean, default: true },
  },
  data() {
    return {
      form: {},
      intervalTimeOptions,
      loading: true,
      time: new Date(dateConvert(dayjs().subtract(7, 'day'))),
      timeType: 3,
      startDate: {},
      selectGrade: '',
      selectCourse: '',
      selectSubject: '',
      grades: [],
      params: {},
      metaDataEmpty: false,
    }
  },
  methods: {
    submit() {
      if (!this.validate() || !this.getData) return
      const { selectCourse, courses, mode } = this
      let params = this.genTimeParams()
      if (mode === 'student') {
        const { id: course, type: courseType } = courses.find(ele => ele.id === selectCourse) || {}
        params = { ...params, course, courseType }
        this.getData(params, { course, empty: this.metaDataEmpty })
      }
      if (mode === 'teacher' || mode === 'resource') {
        const { selectGrade, selectSubject } = this
        params = { ...params, grade: selectGrade, subject: selectSubject }
        this.getData(params)
      }
      if (mode === 'course') {
        const { selectGrade, form } = this
        const { selectClass } = form
        let tmp = {}
        let type
        let selectDescribe
        if (form.course) {
          tmp = { course: form.course }
          type = 4
          selectDescribe = selectClass.courses.find(ele => form.course === ele.id).name
        } else if (form.subject) {
          tmp = { grade: selectGrade, subject: form.subject }
          type = 2
          selectDescribe = `${GRADES[selectGrade]}年级${SUBJECTS[form.subject]}`
        } else if (selectClass && selectClass.id) {
          if (selectClass.type === 'class') {
            tmp = { class: selectClass.id }
            type = 3
            selectDescribe = selectClass.name
          } else {
            tmp = { course: selectClass.id }
            type = 4
            selectDescribe = selectClass.name
          }
        } else {
          tmp = { grade: selectGrade }
          type = 1
          selectDescribe = `${GRADES[selectGrade]}年级`
        }
        params = { ...params, ...tmp }
        this.getData(params, { type, selectDescribe, empty: this.metaDataEmpty })
      }
      this.params = params
    },
    validate() {
      const { time, selectGrade, selectSubject, mode } = this
      const sendErrorInfo = (message) => this.$message({ type: 'error', message })
      if (time === '') {
        sendErrorInfo('请选择时间')
        return false
      }
      if (mode === 'teacher' || mode === 'resource') {
        if (selectSubject === '') {
          sendErrorInfo('请选择科目')
          return false
        }
      }
      if (selectGrade === '') {
        sendErrorInfo('请选择年级')
        return false
      }
      return true
    },
    gradeConvert(grade) {
      return GRADES[grade] || grade
    },
    getMetaData(mode) {
      this.$req.get(metaDataUrlMap[mode]).then(({ data }) => {
        const { startYear, startMonth, startWeek, grades } = data
        this.startDate = { startYear, startMonth, startWeek }
        this.grades = grades
        this.selectGrade = grades[0].grade
        this.$nextTick(() => {
          this.submit()
        })
      }).catch(() => {
        this.$message({ type: 'error', message: '请求班级及相关数据失败' })
      }).finally(() => {
        this.loading = false
      })
    },
    async getStartTime() {
      const url = `/teacher/api/data/${this.mode}/time`
      const { data: result } = await this.$req.get(url)
      this.startDate = result
    },
    async getMetaDataBeta() {
      this.loading = true
      const params = this.genTimeParams()
      const url = `/teacher/api/data/${this.mode}/meta`
      const { data } = await this.$req.get(url, { params })
      this.loading = false
      if (data.grades.length) {
        this.metaDataEmpty = false
      } else {
        this.metaDataEmpty = true
        data.grades = [{ grade: '无可选班级', classes: [], courses: [] }]
      }
      this.grades = this.mode === 'course' ? this.courseDataConvert(data.grades) : data.grades
      this.selectGrade = this.grades[0].grade
      this.$nextTick(() => {
        this.submit()
      })
    },
    genTimeParams() {
      const { timeType, time } = this
      const date = dayjs(time)
      const year = date.year()
      const month = date.month() + 1
      const week = date.week()
      const params = {
        timeType, year,
      }
      if (timeType === 2) params.month = month
      if (timeType === 3) params.week = week
      return params
    },
    courseDataConvert(grades) {
      const { isManager } = this.teacher
      return grades.map(ele => ({
        ...ele,
        subjects: [...new Set(ele.classes
          .reduce((acc, { courses }) => {
            const subjects = courses.map(({ subject }) => subject)
            return acc.concat(subjects)
          }, isManager ? [0] : []).sort())],
        classes: ele.classes.map((e) => ({
          ...e,
          courses: (e.isClassMaster || isManager ? [{ id: 0, name: '所有学科', subject: 0 }] : []).concat(e.courses),
        })),
      }))
    },
  },
  watch: {
    timeType(val) {
      if (val === 1) {
        this.time = dayjs().startOf('year')
      } else if (val === 2) {
        this.time = dayjs().subtract(this.mode === 'resource' ? 0 : 1, 'month').startOf('month')
      } else if (val === 3) {
        this.time = new Date(dateConvert(dayjs().subtract(this.mode === 'resource' ? 0 : 7, 'day')))
      }
    },
    selectGrade() {
      const { courses, subjects, mode } = this
      if (mode === 'student') {
        if (!courses.length) return ''
        this.selectCourse = courses[0].id
      }
      if (mode === 'teacher' || mode === 'resource') {
        if (!subjects.length) return ''
        this.selectSubject = subjects[0].value
      }
      if (mode === 'course') {
        const { classes } = this
        this.form = { ...this.form, selectClass: classes[0] }
      }
    },
    time() {
      if (this.mode === 'course' || this.mode === 'student') {
        this.getMetaDataBeta()
      }
    },
    'form.selectClass': function (val) {
      const findResult = this.grades.find(({ grade }) => grade === this.selectGrade)
      const { form } = this
      if (val.type === 'class') {
        if (val.id === 0) {
          delete form.course
          this.$set(form, 'subject', findResult.subjects[0])
        } else {
          delete form.subject
          this.$set(form, 'course', form.selectClass.courses[0].id)
        }
      } else {
        delete form.subject
        delete form.course
      }
    },
  },
  computed: {
    timeSolt() {
      const { time, timeType } = this
      const selectTime = dayjs(time)
      const formatStr = 'YYYY年MM月DD日'
      if (time) {
        if (timeType === 1) {
          // const end = selectTime.year() === dayjs().year() ? dayjs().startOf('week') : selectTime.endOf('year')
          // return `${selectTime.format(formatStr)}-${end.format(formatStr)}`
          return selectTime.format('YYYY年')
        } else if (timeType === 2) {
          // return `${selectTime.format(formatStr)}-${selectTime.endOf('month').format(formatStr)}`
          return selectTime.format('YYYY年MM月')
        } else if (timeType === 3) {
          const start = selectTime.startOf('week').add(1, 'day').format(formatStr)
          const end = selectTime.endOf('week').add(1, 'day').format(formatStr)
          return `${start}-${end}`
        }
      } else {
        return '????年??月??日-????年??月??日'
      }
    },
    courses() {
      if (this.mode === 'student') {
        const findResult = this.grades.find(({ grade }) => this.selectGrade === grade)
        if (!findResult) return []
        const { isSubjectMaster, courses } = findResult
        const allCourseColumn = { id: 0, name: '所有班级', type: '2050' }
        return isSubjectMaster ? [allCourseColumn, ...courses] : courses
      }
      return []
    },
    classes() {
      // 班级分析
      if (this.mode === 'course') {
        const findResult = this.grades.find(({ grade }) => this.selectGrade === grade)
        if (!findResult) return []
        const { isManager } = this.teacher
        const { subjectMaster, classes, courses } = findResult
        const allClassColumn = { name: '所有班级', id: 0, type: 'class' }
        return [
          ...((subjectMaster || isManager) ? [allClassColumn] : []),
          ...classes.map(ele => ({ ...ele, type: 'class' })),
          ...courses.map(ele => ({ ...ele, type: 'course' })),
        ].map((ele) => ({ ...ele, tag: `${ele.type}-${ele.id}` }))
      }
      //
      return []
    },
    subjects() {
      const { mode } = this
      if (mode === 'teacher' || mode === 'resource' || mode === 'course') {
        const findResult = this.grades.find(({ grade }) => this.selectGrade === grade)
        if (!findResult) return []
        return findResult.subjects.map(ele => ({ label: SUBJECTS[ele] || ele, value: ele }))
      }
      return []
    },
    exportUrl() {
      const { canExport, params, mode } = this
      if (!canExport) return 'javascript: void(0)'
      let tmp
      if (mode === 'course') {
        const { timeType, year, week, month } = params
        tmp = Object.entries({ timeType, year, week, month })
          .filter(([, value]) => value !== undefined)
          .reduce((acc, [key, value]) => Object.assign(acc, { [key]: value }), {})
      } else {
        tmp = params
      }
      return `${exportBaseUrlMap[this.mode]}?${qs.stringify(tmp)}`
    },
    yearPickerOptions() {
      const self = this
      return {
        disabledDate(time) {
          const year = dayjs(time).year()
          return !(year >= self.startDate.startYear && year <= nowDate.year())
        },
      }
    },
    monthPickerOptions() {
      const self = this
      return {
        disabledDate(time) {
          const { startDate } = self
          const date = dayjs(time)
          const start = dayjs(`${startDate.startYear}-${startDate.startMonth}`)
          const end = dayjs().subtract(self.mode === 'resource' ? 0 : 1, 'month')
          const isSame = date.isSame(start) || date.isSame(end)
          const isBetween = date.isAfter(start) && date.isBefore(end)
          const isAfter = end.isAfter(start)
          return !((isSame || isBetween) && isAfter)
        },
      }
    },
    weekPickerOptions() {
      const self = this
      return {
        disabledDate(time) {
          const { startDate: { startYear, startWeek } } = self
          const date = dayjs(time).subtract(1, 'day')
          const startNum = parseInt(startYear + (startWeek >= 10 ? `${startWeek}` : `0${startWeek}`))
          const nowWeek = nowDate.week() - (self.mode === 'resource' ? 0 : 1)
          const endNum = parseInt(`${nowDate.year()}${nowWeek >= 10 ? `${nowWeek}` : `0${nowWeek}`}`)
          const timeNum = parseInt(date.year() + (date.week() >= 10 ? `${date.week()}` : `0${date.week()}`))
          return !(timeNum >= startNum && timeNum <= endNum)
        },
        firstDayOfWeek: 1,
      }
    },
    ...mapState({ teacher: 'teacher' }),
  },
  async mounted() {
    const { mode } = this
    this.time = new Date(dateConvert(dayjs().subtract(mode === 'resource' ? 0 : 7, 'day')))
    if (mode === 'course' || mode === 'student') {
      this.getStartTime()
    } else {
      this.getMetaData(mode)
    }
  },
}
</script>

<style lang="less" scoped>
.sidebar {
  width: 220px;
  display: flex;
  flex-direction: column;
  align-items: center;
  // padding: 45px 15px;
  background: #fff;
  > div {
    width: 100%;
  }
  .filter {
    display: flex;
    flex-direction: column;
    align-items: left;
    margin-bottom: 20px;
    &:first-child {
      border: none;
      flex-direction: row;
      flex-wrap: nowrap;
      justify-content: space-between;
      align-items: center;
      button {
        margin-left: 10px;
        height: 38px;
        line-height: 38px;
        padding: 0 20px;
      }
    }
    > * {
      margin-bottom: 15px;
    }
    > div {
      width: 100%;
    }
  }
  .confirm-btn {
    padding: 10px 45px;
    margin-bottom: 20px;
  }
  .btn-container {
    display: flex;
    justify-content: space-evenly;
    > button {
      margin-bottom: 20px;
    }
  }
  .describe {
    margin-top: -25px;
    margin-bottom: 30px;
  }
  /deep/ .el-form {
    width: 100%;
    .el-select {
      width: 100%;
    }
  }
}
</style>




