<template>
  <el-card>
    <div id="rank-table">
      <el-table :data="tableData" size="mini">
        <el-table-column v-for="({key, value}, index) in columns" :key="index" :prop="key" :label="value" align="center">
          <template slot-scope="{row}">
            <div class="rank-table-cell">
              <div :class="['logo', row.rank <= 3 ? `top${row.rank}` : '']" v-if="key === 'rank'"></div>
              <span>{{ row[key] }}</span>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination v-if="dataSource.length" layout="prev, pager, next" :total="total" :page-size="5" @current-change="currentChangeHandle">
      </el-pagination>
    </div>
  </el-card>
</template>

<script>
export default {
  props: {
    dataSource: { type: Array, default: () => [] },
    columns: { type: Array, default: () => [] },
    total: { type: Number, default: 10 },
  },
  data() {
    return {
      currentPage: 1,
    }
  },
  methods: {
    clickHandle(scope) {
      console.log(scope)
    },
    currentChangeHandle(page) {
      this.currentPage = page
    },
  },
  computed: {
    tableData() {
      if (!this.dataSource.length) return []
      const begin = (this.currentPage - 1) * 5
      const end = begin + 5
      return this.dataSource.slice(begin, end)
    },
  },
}
</script>

<style lang="less">
#rank-table {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin-bottom: -20px;
  .top1 {
    background-image: url('~assets/img/top1.png');
  }
  .top2 {
    background-image: url('~assets/img/top2.png');
  }
  .top3 {
    background-image: url('~assets/img/top3.png');
  }
  .logo {
    background-repeat: no-repeat;
    height: 31px;
    width: 22px;
    margin-left: -31px;
    + span {
      margin-left: 10px;
    }
  }
  .rank-table-cell {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .el-pagination {
    .active {
      color: #000 !important;
    }
    .number {
      color: #409eff;
    }
  }
}
</style>




