<template>
  <el-card>
    <div id="bar-chart-container" ref="barchart" :style="{height, width}"></div>
  </el-card>
</template>

<script>
import chart from '@/etc/echarts'

const genOption = ({ titleText, titleSubText, dataSource, studentCorrect }) => ({
  title: {
    text: titleText,
    subtext: titleSubText,
    left: 'center',
  },
  grid: {
    bottom: 25,
    top: 80,
  },
  xAxis: {
    type: 'category',
    data: dataSource.map(({ rate }, index) => {
      if (index === 0) return `0%-${rate}%`
      return `${rate - 19}%-${rate}%`
    }),
    axisTick: {
      show: false,
    },
  },
  yAxis: {
    type: 'value',
    name: '学生数量',
  },
  series: [{
    data: dataSource.map(({ studentCount }) => studentCount),
    type: 'bar',
    barCategoryGap: '30%',
    itemStyle: {
      normal: {
        color: ({ dataIndex }) => ['#48a7f2', '#ffcc32'][dataIndex % 2],
      },
    },
    label: {
      normal: {
        show: true,
        position: 'top',
        color: '#000',
        // formatter: '{c}人',
        align: 'center',
        formatter({ name, data }) {
          if (studentCorrect === undefined) {
            return `${data}人`
          }
          const interval = name.split('-').map(ele => parseInt(ele))
          if (studentCorrect >= interval[0] && studentCorrect <= interval[1]) {
            return `{hint|该生所在区间}\n${data}人`
          }
          return `${data}人`
        },
        rich: {
          hint: {
            color: 'red',
            lineHeight: 20,
          },
        },
      },
    },
  }],
})

export default {
  props: {
    titleText: { type: String, default: '学生正确率范围分布图' },
    titleSubText: { type: String, default: 'X轴表示正确率, Y轴表示学生人数' },
    dataSource: { type: Array, default: () => [0, 0, 0, 0] },
    height: { type: String, default: '100%' },
    width: { type: String, default: '100%' },
    studentCorrect: { type: Number, default: -1 },
  },
  watch: {
    dataSource(newVal) {
      this.barchart.setOption(genOption({ ...this.$props, dataSource: newVal }))
    },
  },
  mounted() {
    chart.then(([echarts]) => {
      this.barchart = echarts.init(this.$refs.barchart)
      this.barchart.setOption(genOption(this.$props))
    })
  },

}
</script>

<style lang="less" scoped>
#bar-chart-container {
  // width: 100%;
  // height: 500px;
  // 组件外手动指定大小 important
  display: flex;
  justify-content: center;
}
</style>



