export default (v = {}) => {
  const context = require.context('./', false, /^\.\/(?!index)(.+)\.js$/)
  context.keys().forEach(ele => {
    const [, name] = ele.match(/^\.\/(.+)\.js$/)
    if (name) {
      // 方法使用export default方式导出
      v.prototype[`$${name}`] = context(ele).default
    }
  })
}



