<template>
  <el-card>
    <div id="content-container">
      <img :src="require('assets/img/line-chart-perv.png')" @click="changePage(0)" alt="上一个" title="上一个" />
      <div id="chart-container" ref="chartContainer"></div>
      <img :src="require('assets/img/line-chart-next.png')" @click="changePage(1)" alt="下一个" title="下一个" />
    </div>
  </el-card>
</template>

<script>
import chart from '@/etc/echarts'

const INTERVAL = 6

const studentColor = '#ef588f'
const classColor = '#53b5f6'

export default {
  props: {
    dataSource: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    const currentSelect = 1
    const selectData = this.dataSource.slice(currentSelect - 1, currentSelect + INTERVAL)
    return {
      currentSelect,
      option: {
        title: {
          text: '作业正确率分布图',
          subtext: 'X轴表示作业情况, Y轴表示平均正确率, 不包含未提交数据',
          left: 'center',
        },
        tooltip: {
          trigger: 'axis',
          formatter: (params) => {
            const name = params[0].name
            const contentStr = params.map(({ seriesName, value }) => `${seriesName}: ${value}%`).join('<br>')
            return `${name}<br>${contentStr}`
          },
        },
        color: {
          normal: [classColor, studentColor],
        },
        legend: {
          data: ['班级', '该学生'],
          right: '2%',
          top: '50%',
          orient: 'vertical',
        },
        grid: {
          left: '1%',
          right: '9%',
          top: '100',
          bottom: 20,
          containLabel: true,
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: selectData.map(({ name }) => name),
        },
        yAxis: {
          type: 'value',
          max: 100,
          name: '正确率',
          axisLabel: {
            formatter: '{value}%',
          },
        },
        series: [
          {
            name: '班级',
            type: 'line',
            stack: '班级',
            symbol: 'circle',
            smooth: true,
            areaStyle: {
              normal: {
                // color: {
                //   type: 'linear',
                //   x: 0,
                //   y: 0,
                //   x2: 0,
                //   y2: 1,
                //   colorStops: [{
                //     offset: 0, color: classColor,
                //   }, {
                //     offset: 1, color: '#fff',
                //   }],
                //   opacity: 1,
                //   globalCoord: false,
                // },
                color: '#dce5f6',
              },
            },
            lineStyle: {
              normal: {
                color: classColor,
              },
            },
            itemStyle: {
              normal: {
                color: () => classColor,
                borderColor: classColor,
                borderWidth: 5,
              },
            },
            data: selectData.map(({ cAccuracy }) => cAccuracy),
          },
          {
            name: '该学生',
            type: 'line',
            stack: '该学生',
            symbol: 'circle',
            smooth: true,
            areaStyle: {
              normal: {
                // color: {
                //   type: 'linear',
                //   x: 0,
                //   y: 0,
                //   x2: 0,
                //   y2: 1,
                //   colorStops: [{
                //     offset: 0, color: studentColor,
                //   }, {
                //     offset: 1, color: '#fff',
                //   }],
                //   opacity: 1,
                //   globalCoord: false,
                // },
                color: '#ffecf3',
              },
            },
            lineStyle: {
              normal: {
                color: studentColor,
              },
            },
            itemStyle: {
              normal: {
                color: studentColor,
                borderColor: studentColor,
                borderWidth: 5,
              },
            },
            data: selectData.map(({ sAccuracy }) => sAccuracy),
          },
        ],
      },
    }
  },
  methods: {
    changePage(isNext) {
      const { currentSelect, dataSource } = this
      const cantPerv = !isNext && currentSelect === 1
      const cantNext = isNext && currentSelect + INTERVAL === dataSource.length
      if (cantPerv || cantNext) return

      this.currentSelect += (isNext ? 1 : -1)
      this.$nextTick(() => {
        const { propMap, selectData } = this
        const option = this.getNewOption(this.option, {
          xAxisData: propMap(selectData, 'name'),
          seriesData: [
            { name: '该学生', data: propMap(selectData, 'sAccuracy') },
            { name: '班级', data: propMap(selectData, 'cAccuracy') },
          ],
        })
        this.chart.setOption(option)
      })
    },
    getNewOption(opt, { xAxisData, seriesData }) {
      const option = JSON.parse(JSON.stringify(opt))
      const { xAxis, series } = option
      option.xAxis = { ...xAxis, data: xAxisData }
      option.series = series.map(ele => {
        const column = seriesData.find(sele => sele.name === ele.name)
        return ({ ...ele, ...column })
      })
      return option
    },
    propMap(data, prop) {
      return data.map(ele => ele[prop])
    },
  },
  computed: {
    selectData() {
      const start = this.currentSelect - 1
      const end = this.currentSelect + INTERVAL
      return this.dataSource.slice(start, end)
    },
  },
  mounted() {
    chart.then(([echarts]) => {
      this.chart = echarts.init(this.$refs.chartContainer)
      this.chart.setOption(this.option)
      setTimeout(() => {
        this.chart.resize()
      }, 0)
    })
  },
}
</script>

<style lang="less" scoped>
#content-container {
  display: flex;
  align-items: center;
  width: 100%;
  img {
    flex-shrink: 0;
  }
  > div {
    flex-shrink: 1;
    flex-grow: 1;
    // width: 0;
  }
  #chart-container {
    height: 450px;
    width: calc(~'100% - 126px');
    > div {
      width: 100%;
    }
    // width: 100%;
  }
}
</style>




