<template>
  <el-card>
    <div id="prob-chart">
      <pie-chart v-for="(item, index) in chartData" :key="index" v-bind="item"></pie-chart>
    </div>
  </el-card>
</template>

<script>
import PieChart from './pie-chart'

export default {
  components: { PieChart },
  props: {
    dataSource: { type: Array, default: () => [0, 0] },
  },
  computed: {
    chartData() {
      return this.genOption(this.dataSource)
    },
  },
  methods: {
    genOption([correctRate, finishRate]) {
      return [
        {
          option: {
            name: '平均正确率', dataSource: [correctRate, 100 - correctRate], color: '#fdc52e',
          },
          prompt: '完成批改后的试题组和互动资料的平均正确率',
        },
        {
          option: {
            name: '平均作业完成率', dataSource: [finishRate, 100 - finishRate], color: '#61d467',
          },
          prompt: '课后试题组和互动资料的平均提交率',
        },
      ]
    },
  },
}
</script>

<style lang="less" scoped>
#prob-chart {
  display: flex;
  justify-content: space-evenly;
  > div {
    &:last-child {
      margin-left: 10px;
    }
  }
}
</style>



