<template>
  <div id="radar-chart" ref="chartRef">
  </div>
</template>

<script>
import chart from '@/etc/echarts'
import { SUBJECTS } from '@/assets/js/consts/common'

const color = ['#b2eaff', '#fdd4da']

export default {
  props: {
    dataSource: { type: Array },
  },
  methods: {
    dataConvert(d) {
      const data = d.length < 3 ? d.concat(new Array(3 - d.length).fill({ subject: '', correctRate: 0, cmAverageSecond: 0 })) : d
      const cmSecond = data.map(({ cmAverageSecond: e }) => Math.ceil(e / 60))
      const max = Math.max(...cmSecond)
      const cmAverageSecond = cmSecond.map(ele => Math.ceil(ele / max * 100))
      const indicator = data.map(({ subject }) => ({ name: SUBJECTS[subject] || subject, min: 0, max: 100 }))
      const correctRate = data.map(({ correctRate: e }) => e)
      return { indicator, correctRate, cmAverageSecond, cmSecond }
    },
    genOption({ indicator, correctRate, cmAverageSecond, cmSecond }) {
      return {
        color,
        title: {
          text: '作业用时和正确率占比图',
          left: 'center',
        },
        tooltip: {
          formatter({ name, value }) {
            return `${name}<br>${
              indicator
                .filter(({ name: n }) => n !== '')
                .map(({ name: n }, index) => `${n}: ${name === '作业用时(min)' ? cmSecond[index] : value[index]}`)
                .join('<br>')
              }`
          },
          padding: 10,
        },
        legend: {
          data: ['作业用时(min)', '正确率(%)'],
          bottom: 0,
          itemWidth: 14,
          itemHeight: 14,
          itemGap: 28,
          splitLine: {
            show: false,
          },
        },
        grid: {
          height: 100,
        },
        radar: {
          name: {
            textStyle: {
              color: '#333',
            },
          },
          radius: '60%',
          indicator,
          splitArea: {
            areaStyle: {
              color: ['#fff'],
            },
          },
        },
        series: [{
          name: '',
          type: 'radar',
          data: [
            {
              value: cmAverageSecond,
              name: '作业用时(min)',
              itemStyle: {
                normal: {
                  opacity: 0,
                },
              },
              lineStyle: {
                // normal: {
                //   opacity: 0,
                // },
              },
              areaStyle: {
                normal: {
                  color: color[0],
                },
              },
            },
            {
              value: correctRate,
              name: '正确率(%)',
              itemStyle: {
                normal: {
                  opacity: 0,
                },
              },
              lineStyle: {
                // normal: {
                //   opacity: 0,
                // },
              },
              areaStyle: {
                normal: {
                  color: color[1],
                },
              },
            },
          ],
        }],
      }
    },
    setOption() {
      this.chart.setOption(
        this.genOption(
          this.dataConvert(this.dataSource),
        ),
      )
    },
  },
  mounted() {
    chart.then(([echarts]) => {
      this.chart = echarts.init(this.$refs.chartRef)
      this.setOption()
    })
  },
  watch: {
    dataSource() {
      this.setOption()
      this.chart.resize()
    },
  },
}
</script>




