<template>
  <el-card>
    <div id="content-container">
      <div class="title">{{title}}</div>
      <div class="content">
        <div class="empty" v-if="!dataSource.length">无</div>
        <div class="tag" v-for="(item, index) in dataSource" :key="index">{{item}}</div>
      </div>
    </div>
  </el-card>
</template>

<script>
export default {
  props: {
    title: { type: String, default: '薄弱知识点' },
    dataSource: { type: Array, default: () => [] },
  },
}
</script>

<style lang="less" scoped>
#content-container {
  display: flex;
  flex-direction: column;
  font-size: 14px;
  > div {
    width: 100%;
  }
  .title {
    font-size: 18px;
    font-weight: bold;
    text-align: center;
    // padding: 20px 10px 0;
  }
  .content {
    display: flex;
    flex-wrap: wrap;
    padding-top: 20px;
    .empty {
      width: 100%;
      text-align: center;
      font-size: 16px;
    }
    .tag {
      padding: 10px 40px;
      border: 1px solid #dcdcdc;
      border-radius: 5px;
      margin: 5px;
      text-align: center;
      &:hover {
        border: 1px solid #48a7f2;
        color: #48a7f2;
      }
    }
  }
}
</style>



