<template>
  <el-card>
    <div id="radar-chart-content-box">
      <div id="chart-container" ref="chartContainer"></div>
      <div class="describe" v-html="describeText"></div>
    </div>
  </el-card>
</template>

<script>
import chart from '@/etc/echarts'

const genOption = ({ indicator, averageAccuracy, subjectAccuracy }) => ({
  color: ['#ccc', '#fc8126'],
  tooltip: { show: false },
  legend: {
    data: ['正确率65%', '单科正确率'],
    bottom: 0,
    textStyle: {
      fontSize: '18',
    },
  },
  grid: {
    height: 450,
  },
  radar: {
    name: {
      rich: {
        subject: {
          fontSize: '18',
          // fontWeight: 'bold',
          color: '#000',
        },
        percent: {
          fontSize: '18',
          fontWeight: 'bold',
          color: '#ff6101',
        },
      },
      textStyle: {
        color: '#000',
        backgroundColor: '#fff',
      },
    },
    splitNumber: 5,
    splitArea: {
      areaStyle: {
        color: ['#fff'],
      },
    },
    radius: '65%',
    center: ['50%', '45%'],
    indicator,
  },
  series: [{
    type: 'radar',
    data: [
      {
        value: averageAccuracy,
        name: '正确率65%',
        areaStyle: {
          normal: {
            color: '#ccc',
          },
        },
        itemStyle: {
          normal: {
            opacity: 0,
          },
        },
        lineStyle: {
          normal: {
            opacity: 0,
          },
        },
      },
      {
        value: subjectAccuracy,
        name: '单科正确率',
        areaStyle: {
          normal: {
            color: '#fc8126',
          },
        },
        itemStyle: {
          normal: {
            opacity: 0,
          },
        },
        lineStyle: {
          normal: {
            opacity: 0,
          },
        },
      },
    ],
  }],
})

export default {
  props: {
    dataSource: { type: Array, default: () => new Array(6).fill({ name: undefined }) },
    name: { type: String, default: '' },
  },
  computed: {
    describeText() {
      const { dataSource, name } = this
      if (!dataSource.length) return '<span class="red">没有科目相关数据</span>'
      const accuracyData = dataSource.map(({ accuracy }) => accuracy)
      const maxAccuracy = Math.max(...accuracyData)
      const minAccuracy = Math.min(...accuracyData)
      const isAverage = maxAccuracy - minAccuracy >= 30
      const subjects = dataSource.reduce((acc, { subject, accuracy }) => {
        if (accuracy > 85) return { ...acc, excellent: acc.excellent.concat(`<span class="red">${subject}</span>`) }
        if (accuracy < 60) return { ...acc, weak: acc.weak.concat(`<span class="red">${subject}</span>`) }
        return { ...acc, normal: acc.normal.concat(`<span class="red">${subject}</span>`) }
      }, {
          excellent: [],
          normal: [],
          weak: [],
        },
      )
      return this.genDescirbeText({
        ...subjects,
        name,
        isAverage,
      })
    },
  },
  methods: {
    genDescirbeText({ name, isAverage, excellent, normal, weak }) {
      const weakText = weak.length ? `${weak.join('、')}学科明显偏弱` : ''
      const excellentText = excellent.length ? `${excellent.join('、')}学科成绩优秀` : ''
      const normalText = normal.length ? `${normal.join('、')}学科成绩一般` : ''
      const averageText = isAverage ? '均匀' : '不均匀'
      const firstSentence = `${name}同学科目分布相对${averageText}`
      const describeText = [firstSentence, weakText, excellentText, normalText].filter(ele => ele.length).join('，')
      return `${describeText}。`
    },
    genchartData() {
      let indicator = this.dataSource.map(({ subject, accuracy }) => ({
        name: `{subject|${subject}}\n{percent|${accuracy}%}`,
        max: 103,
      }))
      let subjectAccuracy = this.dataSource.map(({ accuracy }) => (accuracy + 3))
      if (indicator.length < 3) {
        subjectAccuracy = subjectAccuracy.concat(new Array(3 - indicator.length).fill(3))
        indicator = [...indicator, ...new Array(3 - indicator.length).fill({ name: '{percent|0}', max: 103 })]
      }
      const averageAccuracy = new Array(indicator.length).fill(68)
      return { indicator, averageAccuracy, subjectAccuracy }
    },
  },
  watch: {
    dataSource() {
      setTimeout(() => {
        this.chart.setOption(genOption(this.genchartData()))
      }, 0)
    },
  },
  mounted() {
    chart.then(([echarts]) => {
      this.chart = echarts.init(this.$refs.chartContainer)
      this.chart.setOption(genOption(this.genchartData()))
    })
  },
}
</script>

<style lang="less">
#radar-chart-content-box {
  display: flex;
  align-items: center;
  #chart-container {
    width: 50%;
    height: 470px;
  }
  .describe {
    font-size: 22px;
    // font-weight: bold;
    line-height: 1.7;
    .red {
      color: #ff0000;
    }
  }
  .red {
    color: #ff0000;
  }
}
</style>




